          display: flex;
          justify-content: flex-start;
          flex-wrap: wrap;
          img {
            -webkit-transition: all 0.5s;
            -moz-transition: all 0.5s;
            transition: all 0.5s;
            width: 100%;
            top: 0;
            position: relative;
            left: 0;
          }
          img:hover {
            transform: scale(1.04);
            cursor: pointer;
          }
          .flex-img-item {
            max-width: 33%;
            margin-bottom: .33333%;
            margin-right: .33333%;
            // margin: 0 2px 2px 0px;
            background: #fff;
            overflow: hidden;
            position: relative;
          }
        }
      }
    }
  }
