.show {
  height: auto;
}

.hide {
  height: calc(100vh - 100px) !important;
  overflow: hidden;
}

.maskDiv {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10000;
  background: rgba(199, 203, 189, 1);
  width: 100%;
  height: calc(100vh - 80px);
}
</style>


